<template>
  <div>
    <b-modal
      size="md"
      ref="modal_ref"
      @ok="submitTest"
      ok-title="Submit Assessment"
      id="confirm-modal"
      title="Confirm"
      content-class="bg-soft-light submit-modal"
      header-class="border-bottom-0"
      footer-class="d-flex py-3"
      cancel-variant="secondary btn-sm px-3 mr-2 py-1"
      ok-variant="primary btn-sm px-4 py-1"
      body-class="py-3"
      hide-header
    >
      <h3 class="my-3 line-height-14 text-danger">Are you sure you want to submit the assessment?</h3>
      <div v-if="total_questions / 2 > answered_questions || is_mandatory_validation" class="d-flex ">
        <p class=" d-inline-block mb-0 px-3 py-2 my-2 text-body rounded small custom-alert-warning">
          We noticed that you have not filled in
          <span v-if="total_questions / 2 > answered_questions">all the answers</span>
          <span v-if="total_questions / 2 > answered_questions && is_mandatory_validation" class="ml-2">&</span>
          <span v-if="is_mandatory_validation"> mandatory (*) questions.</span>
        </p>
      </div>

      <p class="mt-3">
        <span class="mb-1 mt-3 text-muted">
          Total Questions :<span class="font-weight-bold text-gray-900 ml-2">{{ getTotalCount(categories) }}</span>
        </span>
        <span class="text-muted ml-3">
          Your Answers :<span class="font-weight-bold text-gray-900 ml-2">{{ getAttemptedCount(attempt_count) }}</span>
        </span>
      </p>
      <div class="">
        Notes <small class="text-muted">(Optional)</small>
        <b-form-textarea id="textarea" v-model="candidate_notes" placeholder="Please enter notes..." rows="3" max-rows="6"></b-form-textarea>
      </div>
    </b-modal>
  </div>
</template>

<script>
  export default {
    props: { attempt_count: Object, total_questions_count: Number, categories: Array, is_mandatory_validation: Boolean },
    data() {
      return {
        answered_questions: 0,
        total_questions: 0,
        candidate_notes: "",
      };
    },

    methods: {
      // Open modal
      open() {
        this.$refs["modal_ref"].show();
      },
      // Submit the test
      submitTest() {
        this.$emit("submit", this.candidate_notes);
      },
      // Get attempted questions length
      getAttemptedCount(attempt_count) {
        let attempted_count = 0;
        Object.values(attempt_count).map((count) => {
          attempted_count = attempted_count + count;
        });
        this.answered_questions = attempted_count;
        return attempted_count;
      },
      // Get total questions length
      getTotalCount(categories) {
        let question_count = 0;
        categories.forEach((category) => {
          question_count = question_count + category.questions.length;
        });
        this.total_questions = question_count;
        return question_count;
      },
    },
  };
</script>
<style>
  .modal-backdrop {
    opacity: 0.5;
  }
  .submit-modal {
    max-width: 600px !important;
  }
  .custom-alert-warning {
    color: #283e59 !important;
    background-color: #fff8e7 !important;
    border-color: #f6c343 !important;
  }
</style>
